  padding: 0;
  margin: 0;
  font-size: inherit;
  font-weight: inherit;
  box-sizing: border-box;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}

header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 4rem;
  background-color: #101820;
  color: #fff;
  position: fixed;
  width: 100%;
  z-index: 10;
}
header h1 {
  font-size: 1.8rem;
  font-family: "Whisper", sans-serif;
  font-weight: 700;
  cursor: pointer;
  transition: 0.3s ease-in-out;
  position: relative;
  padding: 0px 8px;
}
header h1:hover {
  transform: scale(1.1);
}
header h1::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  height: 0;
  width: 1px;
  background-color: #FEE715;
  transition: 0.3s ease-in-out;
}
header h1:hover::before {
  height: 100%;
}
header h1::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  height: 0;
  width: 1px;
  background-color: #FEE715;
  transition: 0.3s ease-in-out;
}
header h1:hover::after {
  height: 100%;
}
header .menu-container {
  display: flex;
  flex-direction: row;
  list-style: none;
  gap: 20px;
}
header .menu-container li {
  cursor: pointer;
  position: relative;
  padding-bottom: 4px;
}
header .menu-container li:hover::after {
  width: 100%;
}
header .menu-container li::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 0;
  height: 1px;
  background-color: #FEE715;
  transition: 0.3s ease-in-out;
}
header .menu-container li a {
  color: white;
  text-decoration-line: none;
}

#home {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #ff889a;
  color: #fff;
  height: 90vh;
}
#home h1 {
  font-family: "Whisper", sans-serif;
  font-weight: 700;
  font-size: 4rem;
  padding-bottom: 2rem;
}
#home h2 {
  font-weight: 500;
  animation: 2s appear-text-right 1 alternate;
}
#home p {
  animation: 2s appear-text-left 1 alternate;
}
#home h2, #home p {
  font-size: 2.5rem;
}
#home a {
  text-transform: uppercase;
  text-decoration: none;
  color: #FEE715;
  position: relative;
}
#home a::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 0;
  height: 2px;
  background-color: #FEE715;
  transition: 0.3s ease-in-out;
}
#home a:hover::after {
  width: 100%;
}

#about {
  padding: 3rem 4rem;
  background-color: #101820;
  color: #fff;
}
#about h2 {
  font-size: 2.5rem;
  letter-spacing: 1px;
  margin-bottom: 1rem;
  font-weight: 500;
}
#about p {
  font-size: 0.9rem;
  line-height: 1.5rem;
}
#about .skills-container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  gap: 20px;
  margin-top: 2.5rem;
  padding: 0rem 8rem;
}
#about .skills-container div {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 150px;
  border: 1px solid #FEE715;
  border-radius: 16px;
  position: relative;
  cursor: pointer;
  transition: 0.3s ease-in-out;
}
#about .skills-container div h4 {
  position: absolute;
  top: 1%;
  font-weight: 500;
}
#about .skills-container div:hover {
  background-color: #cab811;
}
#about .skills-container .skill {
  width: 100px;
  padding: 1rem;
}

#my-work {
  background-color: rgb(228, 228, 228);
  padding: 3rem 4rem;
}
#my-work h2 {
  font-size: 2.5rem;
  letter-spacing: 1px;
  font-weight: 500;
}

@keyframes appear-text-right {
  from {
    transform: translateX(100vw);
  }
  to {
    transform: translateX(0);
  }
}
@keyframes appear-text-left {
  from {
    transform: translateX(-100vw);
  }
  to {
    transform: translateX(0);
  }
